.navbar{
    height: 4rem;
}

.navbar-brand{
    font-weight: bold;
}

.project{
    color: goldenrod;
}

.active-link{
    font-weight: bold;
}
.clock-container{
    margin: 2rem 0;
}

.clock{
    width: 15rem;
    height: 15rem;
    border: 3px darkslategray solid;
    border-radius: 50%;
    display:flex;
    justify-content: center;
    align-items: center;
    background: black;
}

#clock-text{
    font-size: 3rem;
    color: #fff;
}

#controls{
    display:flex;
    justify-content: space-between;
    width: 12rem;
    margin: 0 auto;
}

button{
    width: 5rem;
}
.form-group{
    width: 15rem;
}

input:focus::-webkit-input-placeholder{
    color: transparent;
}

input:focus::-moz-placeholder{
    color:transparent;
}

input:focus::-ms-input-placeholder{
    color:transparent;
}

.btn{
    margin-top: 1rem;
}
.container{
    padding-top: 2rem;
}

